@import url('https://fonts.googleapis.com/css?family=Anton');
@import url('https://fonts.googleapis.com/css?family=Roboto');

html {
	height: 100vh;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(80%, rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, .2)));
	background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 80%, rgba(0, 0, 0, .2) 100%);
	background: -o-linear-gradient(top, rgba(0, 0, 0, 0) 80%, rgba(0, 0, 0, .2) 100%);
	background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 80%, rgba(0, 0, 0, .2) 100%);
	font-family: 'Anton';
}

body {
	max-width: 1500px;
	margin: auto;
	margin-top: 70px;
}

::-webkit-scrollbar {
display: none;
}


